import { Injectable } from '@angular/core';
import { FormFieldConfig } from '../../models';
@Injectable({
  providedIn: 'root',
})
export class FormPathService {
  /**
   * Constructs a full path by combining parent path and key
   */
  getFullPath(key: string, parentPath: string): string {
    return parentPath ? `${parentPath}.${key}` : key;
  }

  /**
   * Returns a field with its full path set
   */
  getFieldWithPath(
    field: FormFieldConfig,
    parentPath: string,
  ): FormFieldConfig {
    return {
      ...field,
      key: this.getFullPath(field.key, parentPath),
    };
  }

  /**
   * Extracts parent path from a full path
   * e.g., "parent.child.field" -> "parent.child"
   */
  getParentPath(fullPath: string): string {
    const parts = fullPath.split('.');
    parts.pop();
    return parts.join('.');
  }

  /**
   * Extracts field name from full path
   * e.g., "parent.child.field" -> "field"
   */
  getFieldName(fullPath: string): string {
    const parts = fullPath.split('.');
    return parts.at(-1) as string;
  }

  /**
   * Checks if a path is a child of another path
   */
  isChildPath(childPath: string, parentPath: string): boolean {
    if (!parentPath) return true;
    return childPath.startsWith(`${parentPath}.`);
  }

  /**
   * Gets the depth of a path (number of dots + 1)
   */
  getPathDepth(path: string): number {
    return path ? path.split('.').length : 0;
  }
}
