import { Injectable, inject, DestroyRef } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { DialogService, DynamicDialogRef } from 'primeng/dynamicdialog';
import { MessageService } from 'primeng/api';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { Observable } from 'rxjs';

import {
  ListFieldModalConfig,
  FormFieldConfig,
  FieldConfiguration,
} from '../../models/form-field.model';
import { isDefined, isFunction, isNullOrUndefined } from '../../utils';

/**
 * Result of a modal submission - either new item or updated item
 */
export interface ModalSubmitResult<T = unknown> {
  data: T;
  isEdit: boolean;
  index?: number;
}

/**
 * Configuration for opening a list item modal
 */
export interface OpenModalConfig<T = unknown> {
  field: FormFieldConfig;
  modalConfig: ListFieldModalConfig<T>;
  itemData: T;
  editIndex?: number;
  destroyRef: DestroyRef;
}

/**
 * Service to manage modal lifecycle for list fields
 * Handles opening modals, generating titles, managing submissions, and formatting items
 */
@Injectable({
  providedIn: 'root',
})
export class ModalFormService {
  private readonly dialogService = inject(DialogService);
  private readonly messageService = inject(MessageService);

  private activeDialogRef: DynamicDialogRef | null = null;

  /**
   * Opens a modal for adding or editing a list item
   * Returns an Observable that emits the submitted data
   */
  openListItemModal<T = unknown>(
    config: OpenModalConfig<T>,
  ): Observable<ModalSubmitResult<T>> {
    const { modalConfig, itemData, editIndex, destroyRef } = config;

    return new Observable((observer) => {
      // Close any existing modal
      this.activeDialogRef?.close();

      // Generate modal header
      const modalHeader = this.generateModalHeader(
        modalConfig.modalTitle as string | ((item?: unknown) => string),
        itemData,
        isDefined(editIndex),
      );

      // Prepare modal fields
      const modalFields = this.resolveModalFields(
        modalConfig.modalFields as
          | FormFieldConfig[]
          | ((item?: unknown) => FormFieldConfig[]),
        itemData,
      );
      let modalFieldsFn: (() => FormFieldConfig[]) | undefined;

      if (isFunction(modalConfig.modalFields)) {
        modalFieldsFn = () =>
          this.resolveModalFields(
            modalConfig.modalFields as
              | FormFieldConfig[]
              | ((item?: unknown) => FormFieldConfig[]),
            itemData,
          );
      }

      // Open dialog with configured actions
      this.activeDialogRef = this.dialogService.open(
        modalConfig.modalComponent,
        {
          header: modalHeader,
          contentStyle: {
            'max-height': '80vh',
            overflow: 'auto',
            'min-width': '40rem',
          },
          style: { width: 'auto' },
          baseZIndex: 10000,
          focusOnShow: false,
          modal: true,
          duplicate: true,
          data: {
            fields: modalFields,
            fieldsFn: modalFieldsFn,
            initialData: itemData,
            subHeader: modalConfig.subHeader,
            actions: this.buildModalActions(
              modalConfig as ListFieldModalConfig<unknown>,
              isDefined(editIndex),
            ),
          },
        },
      );

      // Subscribe to modal close event
      if (this.activeDialogRef) {
        this.activeDialogRef.onClose
          .pipe(takeUntilDestroyed(destroyRef))
          .subscribe((submittedData: unknown) => {
            this.activeDialogRef = null;

            if (isDefined(submittedData)) {
              const result: ModalSubmitResult<T> = {
                data: submittedData as T,
                isEdit: isDefined(editIndex),
                index: editIndex,
              };

              observer.next(result);
              observer.complete();
            } else {
              // Modal was closed without submission
              observer.complete();
            }
          });
      }
    });
  }

  /**
   * Generates the modal header based on config and operation type
   */
  private generateModalHeader(
    titleConfig: string | ((item?: unknown) => string),
    itemData: unknown,
    isEdit: boolean,
  ): string {
    if (isFunction(titleConfig)) {
      return titleConfig(itemData);
    }

    // For static titles, swap "Add"  "Edit" based on operation
    if (isEdit) {
      return titleConfig.replace(/^Add\s+/, 'Edit ') === titleConfig
        ? `Edit ${titleConfig}`
        : titleConfig.replace(/^Add\s+/, 'Edit ');
    }

    return titleConfig;
  }

  /**
   * Resolves modal fields - handles both array and function formats
   */
  private resolveModalFields(
    fieldsConfig: FormFieldConfig[] | ((item?: unknown) => FormFieldConfig[]),
    itemData: unknown,
  ): FormFieldConfig[] {
    if (isFunction(fieldsConfig)) {
      return fieldsConfig(itemData);
    }
    return fieldsConfig;
  }

  /**
   * Builds the modal action buttons
   */
  private buildModalActions(
    modalConfig: ListFieldModalConfig<unknown>,
    isEdit: boolean,
  ): Record<string, unknown>[] {
    return [
      {
        label: 'Cancel',
        severity: 'secondary',
        type: 'cancel',
        action: () => this.activeDialogRef?.close(),
      },
      {
        label: isEdit ? 'Update' : 'Add',
        severity: 'primary',
        type: 'submit',
        action: async (submittedData: FormGroup) => {
          if (!this.activeDialogRef) return;

          if (isFunction(modalConfig.onSubmit)) {
            try {
              const result = await modalConfig.onSubmit(
                submittedData.value,
                this.activeDialogRef,
              );

              // Only close if onSubmit didn't explicitly return false
              if (result !== false && isDefined(this.activeDialogRef)) {
                this.activeDialogRef.close(submittedData.getRawValue());
              }
            } catch (error) {
              console.error('Error in modalConfig.onSubmit:', error);
            }
          } else {
            this.activeDialogRef.close(submittedData.getRawValue());
          }
        },
      },
    ];
  }

  /**
   * Formats a list item for display using the configured formatter or defaults
   */
  formatListItem<T = unknown>(
    item: T,
    formatter?: (item: T) => FieldConfiguration,
  ): FieldConfiguration {
    if (formatter) {
      try {
        return formatter(item);
      } catch (error) {
        console.error('Error in listItemFormatter:', error);
        return this.defaultItemFormatter(item);
      }
    }

    return this.defaultItemFormatter(item);
  }

  /**
   * Default formatter that intelligently picks common field names
   * Falls back to stringifying the first property if no common names found
   */
  private defaultItemFormatter(item: unknown): FieldConfiguration {
    if (isNullOrUndefined(item)) return { id: '', display: '' };

    const primitiveResult = this.formatPrimitive(item);
    if (primitiveResult) return primitiveResult;

    if (typeof item !== 'object') {
      const fallback = JSON.stringify(item);
      return { id: fallback, display: fallback };
    }

    const obj = item as Record<string, unknown>;

    // Try common display field names in priority order
    const displayFields = ['name', 'label', 'title', 'displayName', 'display'];
    for (const field of displayFields) {
      const value = obj[field];
      if (typeof value === 'string' && value.length > 0) {
        const idField = 'id' in obj ? String(obj['id']) : String(value);
        return {
          id: idField,
          display: value,
        };
      }
    }

    // Fallback: use first property
    const firstKey = Object.keys(obj)[0];
    if (firstKey) {
      const firstValue = obj[firstKey];
      const displayValue =
        typeof firstValue === 'string'
          ? firstValue
          : JSON.stringify(firstValue ?? '[Item]');
      return {
        id: 'id' in obj ? String(obj['id']) : firstKey,
        display: displayValue,
      };
    }

    return { id: '', display: '[Item]' };
  }

  /**
   * Formats primitive values into a FieldConfiguration if possible
   * Returns null for non-primitive values so caller can continue processing
   */
  private formatPrimitive(item: unknown): FieldConfiguration | null {
    switch (typeof item) {
      case 'string':
        return { id: item, display: item };
      case 'number': {
        const numeric = String(item);
        return { id: numeric, display: numeric };
      }
      case 'boolean': {
        const boolString = item ? 'true' : 'false';
        return { id: boolString, display: boolString };
      }
      case 'bigint': {
        const bigIntString = item.toString();
        return { id: bigIntString, display: bigIntString };
      }
      case 'symbol': {
        const symbolString = item.toString();
        return { id: symbolString, display: symbolString };
      }
      case 'undefined':
        return { id: '', display: '' };
      default:
        return null;
    }
  }

  /**
   * Shows a notification message after modal submission
   */
  showSubmissionNotification(
    isEdit: boolean,
    hideSaveNotification?: boolean,
  ): void {
    if (hideSaveNotification === true) return;

    const summary = isEdit ? 'Item Updated' : 'Item Added';
    this.messageService.add({
      severity: 'info',
      summary,
      detail: 'Click "Save" to save your changes.',
      life: 5000,
    });
  }

  /**
   * Shows a notification for item deletion
   */
  showDeletionNotification(): void {
    this.messageService.add({
      severity: 'info',
      summary: 'Item Deleted',
      detail: 'Click "Save" to save your changes.',
    });
  }

  /**
   * Closes any active dialog
   */
  closeActiveDialog(): void {
    this.activeDialogRef?.close();
    this.activeDialogRef = null;
  }
}
