import { DestroyRef, Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import {
  FieldDependency,
  FormFieldConfig,
  DependencyValue,
} from '../../models/form-field.model';

@Injectable({
  providedIn: 'root',
})
export class FormDependencyService {
  /**
   * Set up all dependencies for fields in the given sections
   */
  handleDependencies(
    sections: { key: string; title: string; fields: FormFieldConfig[] }[],
    form: FormGroup,
    destroyRef: DestroyRef,
  ): void {
    for (const section of sections) {
      for (const field of section.fields) {
        if (field.dependencies) {
          this.setupFieldDependencies(field, form, destroyRef);
        }
      }
    }
  }

  /**
   * Set up dependencies for a single field
   */
  setupFieldDependencies(
    field: FormFieldConfig,
    form: FormGroup,
    destroyRef: DestroyRef,
  ): void {
    for (const dep of field.dependencies ?? []) {
      form
        .get(dep.field)
        ?.valueChanges.pipe(takeUntilDestroyed(destroyRef))
        .subscribe((value) =>
          this.handleDependencyAction(field, dep, value, form),
        );
    }
  }

  /**
   * Handle the action triggered by a dependency value change
   */
  handleDependencyAction(
    field: FormFieldConfig,
    dep: FieldDependency,
    value: DependencyValue,
    form: FormGroup,
  ): void {
    // Check if the value matches (considering invertMatch)
    const matches =
      (dep.invertMatch ?? false) ? value !== dep.value : value === dep.value;

    if (matches) {
      switch (dep.action) {
        case 'show':
          field.visible = true;
          break;
        case 'hide':
          field.visible = false;
          break;
        case 'enable':
          form.get(field.key)?.enable();
          break;
        case 'disable':
          form.get(field.key)?.disable();
          break;
      }
    } else {
      // If it doesn't match, apply the opposite action
      switch (dep.action) {
        case 'show':
          field.visible = false;
          break;
        case 'hide':
          field.visible = true;
          break;
        case 'enable':
          form.get(field.key)?.disable();
          break;
        case 'disable':
          form.get(field.key)?.enable();
          break;
      }
    }
  }
}
