import { Injectable, inject, Signal } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { FormFieldConfig } from '../../models/form-field.model';
import { isDefined } from '../../utils';
import { FormStructureService } from './form-structure.service';

export interface ConditionalFieldContext {
  form: FormGroup;
  dynamicFields: Signal<FormFieldConfig[]>;
  updateDynamicFields: (
    updater: (fields: FormFieldConfig[]) => FormFieldConfig[],
  ) => void;
}

@Injectable({
  providedIn: 'root',
})
export class ConditionalFieldService {
  private readonly formStructureService = inject(FormStructureService);

  /**
   * Main entry point for handling field value changes that trigger conditional fields
   */
  handleFieldChange(
    field: FormFieldConfig,
    value: string | number,
    parentPath: string,
    context: ConditionalFieldContext,
    onFieldChange: (
      field: FormFieldConfig,
      value: string | number,
      path: string,
    ) => void,
  ): void {
    // Early return if conditions not met
    if (
      field.conditional !== true ||
      !isDefined(field.conditions) ||
      !isDefined(field.conditions[value])
    ) {
      return;
    }

    try {
      // Step 1: Prepare field maps
      const { conditionalFields, previousFields } = this.prepareFieldMaps(
        field,
        value,
      );

      // Step 2: Handle group preservation and field removal
      const { fieldsToRemove, groupsToPreserve } = this.handleFieldPreservation(
        conditionalFields,
        previousFields,
      );

      // Step 3: Process form structure changes
      this.processFormStructureChanges(
        conditionalFields,
        previousFields,
        fieldsToRemove,
        groupsToPreserve,
        parentPath,
        context.form,
      );

      // Step 4: Update dynamic fields
      this.updateDynamicFieldsWithConditionals(
        conditionalFields,
        fieldsToRemove,
        groupsToPreserve,
        parentPath,
        context,
      );

      // Step 5: Process nested conditionals
      this.processNestedConditionalTriggers(
        conditionalFields,
        parentPath,
        onFieldChange,
      );
    } catch (error) {
      console.error('Error in handleFieldChange:', error);
    }
  }

  /**
   * Prepare maps of conditional and previous fields
   */
  prepareFieldMaps(
    field: FormFieldConfig,
    value: string | number,
  ): {
    conditionalFields: FormFieldConfig[];
    previousFields: FormFieldConfig[];
  } {
    // Get fields for new condition
    const conditionalFields = field.conditions?.[value] ?? [];

    // Get fields for all other conditions
    const previousFields = Object.entries(field.conditions ?? {})
      .filter(([k]) => k !== String(value))
      .flatMap(([_, fields]) => fields);

    return { conditionalFields, previousFields };
  }

  /**
   * Handle group preservation and collect fields to remove
   */
  handleFieldPreservation(
    conditionalFields: FormFieldConfig[],
    previousFields: FormFieldConfig[],
  ): {
    fieldsToRemove: Set<string>;
    groupsToPreserve: Set<string>;
  } {
    const fieldsToRemove = new Set<string>();
    const groupsToPreserve = new Set<string>();

    // Create lookup map for new fields
    const newFieldsMap = new Map<string, FormFieldConfig>();
    for (const f of conditionalFields) {
      newFieldsMap.set(f.name, f);
    }

    // Find groups to preserve and fields to remove
    for (const oldField of previousFields) {
      if (oldField.type === 'group' && newFieldsMap.has(oldField.name)) {
        groupsToPreserve.add(oldField.name);
      }
      this.collectFieldsToRemove(oldField, fieldsToRemove);
    }

    return { fieldsToRemove, groupsToPreserve };
  }

  /**
   * Process form structure changes
   */
  private processFormStructureChanges(
    conditionalFields: FormFieldConfig[],
    previousFields: FormFieldConfig[],
    fieldsToRemove: Set<string>,
    groupsToPreserve: Set<string>,
    parentPath: string,
    form: FormGroup,
  ): void {
    // Remove fields that should be completely removed
    for (const fieldToRemove of previousFields) {
      if (
        !(
          fieldToRemove.type === 'group' &&
          groupsToPreserve.has(fieldToRemove.name)
        )
      ) {
        this.formStructureService.removeFieldFromForm(
          form,
          fieldToRemove,
          parentPath,
        );
      }
    }

    // Build conditional form group hierarchy
    this.formStructureService.buildConditionalFormGroupHierarchy(
      form,
      conditionalFields,
      parentPath,
    );

    // Clear shared groups but keep structure
    for (const groupName of groupsToPreserve) {
      const groupPath = parentPath ? `${parentPath}.${groupName}` : groupName;
      this.formStructureService.clearFormGroup(form, groupPath);
    }
  }

  /**
   * Update dynamic fields with conditional fields
   */
  private updateDynamicFieldsWithConditionals(
    conditionalFields: FormFieldConfig[],
    fieldsToRemove: Set<string>,
    groupsToPreserve: Set<string>,
    parentPath: string,
    context: ConditionalFieldContext,
  ): void {
    const currentFields = context.dynamicFields();

    // Filter out fields that should be removed
    const updatedFields = currentFields.filter(
      (f) => !fieldsToRemove.has(f.name) || groupsToPreserve.has(f.name),
    );

    // Create array for final fields
    const finalFields: FormFieldConfig[] = [];

    // Process preserved groups and existing fields
    for (const field of updatedFields) {
      if (groupsToPreserve.has(field.name) && field.type === 'group') {
        this.updateGroupField(field, conditionalFields, finalFields);
      } else {
        finalFields.push(field);
      }
    }

    // Add new fields not already present
    this.addNewFields(conditionalFields, updatedFields, finalFields);

    // Update dynamic fields using the updater function
    context.updateDynamicFields(() => finalFields);

    // Add fields to form structure
    this.formStructureService.addFieldsToForm(
      context.form,
      conditionalFields,
      parentPath,
    );
  }

  /**
   * Update a group field with new content
   */
  private updateGroupField(
    groupField: FormFieldConfig,
    conditionalFields: FormFieldConfig[],
    finalFields: FormFieldConfig[],
  ): void {
    // Find new version of this group
    const newVersion = conditionalFields.find(
      (f) => f.name === groupField.name,
    );

    if (newVersion) {
      // Use new version but keep metadata
      finalFields.push({
        ...groupField,
        fields: [...(newVersion.fields ?? [])],
      });
    } else {
      finalFields.push({ ...groupField, fields: [] });
    }
  }

  /**
   * Add new fields not already present
   */
  private addNewFields(
    newFields: FormFieldConfig[],
    existingFields: FormFieldConfig[],
    finalFields: FormFieldConfig[],
  ): void {
    for (const newField of newFields) {
      if (!existingFields.some((f) => f.name === newField.name)) {
        finalFields.push(newField);
      }
    }
  }

  /**
   * Process nested conditional triggers
   */
  processNestedConditionalTriggers(
    fields: FormFieldConfig[],
    parentPath: string,
    onFieldChange: (
      field: FormFieldConfig,
      value: string | number,
      path: string,
    ) => void,
  ): void {
    for (const field of fields) {
      if (field.conditional === true && field.conditions) {
        const defaultValue = field.value as string | number;

        if (
          isDefined(defaultValue) &&
          isDefined(field.conditions[defaultValue])
        ) {
          const childPath = parentPath
            ? `${parentPath}.${field.name}`
            : field.name;

          // Use setTimeout to avoid stack overflow with deep nesting
          setTimeout(() => {
            onFieldChange(field, defaultValue, childPath);
          }, 0);
        }
      }
    }
  }

  /**
   * Recursively collect a field and all its descendants' names
   */
  collectFieldsToRemove(field: FormFieldConfig, fieldNames: Set<string>): void {
    // Add this field
    fieldNames.add(field.name);

    // Process conditional fields
    if ((field.conditional ?? false) && field.conditions) {
      for (const condField of Object.values(field.conditions).flat()) {
        this.collectFieldsToRemove(condField, fieldNames);
      }
    }

    // Process group fields
    if (field.type === 'group' && field.fields) {
      for (const childField of field.fields) {
        this.collectFieldsToRemove(childField, fieldNames);
      }
    }
  }

  /**
   * Process initial conditional fields based on default values
   */
  processInitialConditionalFields(
    fields: FormFieldConfig[],
    form: FormGroup,
    callbacks: {
      processConditionalFieldsAtLevel: (
        fields: FormFieldConfig[],
        parentPath?: string,
      ) => void;
    },
  ): void {
    // First: build the complete form group hierarchy
    this.formStructureService.buildFormGroupHierarchy(form, fields);

    // Then: Process top-level conditional fields
    callbacks.processConditionalFieldsAtLevel(fields);
  }

  /**
   * Process conditional fields at a given level of the form structure
   */
  processConditionalFieldsAtLevel(
    fields: FormFieldConfig[],
    parentPath: string,
    form: FormGroup,
    callbacks: {
      addConditionalFields: (
        fields: FormFieldConfig[],
        parentPath: string,
      ) => void;
      processNestedConditionalFields: (
        fields: FormFieldConfig[],
        parentPath: string,
      ) => void;
    },
  ): void {
    // Find all conditional fields at this level
    const conditionalFields = fields.filter(
      (field) => field.conditional === true && isDefined(field.conditions),
    );

    // Process each conditional field
    for (const field of conditionalFields) {
      // Get full path for this field
      const fullPath = parentPath ? `${parentPath}.${field.name}` : field.name;

      // Get control for this path
      const control = form.get(fullPath);
      if (!control) {
        return;
      }

      const initialValue = control.value;
      if (isDefined(initialValue) && field.conditions?.[initialValue]) {
        // Add the conditional fields based on this value
        const conditionalFieldsToAdd = field.conditions[initialValue] ?? [];
        callbacks.addConditionalFields(conditionalFieldsToAdd, parentPath);

        // Recursively process any nested conditionals
        callbacks.processNestedConditionalFields(
          conditionalFieldsToAdd,
          parentPath,
        );
      }
    }

    // Second pass: Process group fields to find deeper conditionals
    for (const field of fields) {
      if (field.type === 'group' && field.fields) {
        const groupPath = parentPath
          ? `${parentPath}.${field.name}`
          : field.name;
        this.processConditionalFieldsAtLevel(
          field.fields,
          groupPath,
          form,
          callbacks,
        );
      }
    }
  }

  /**
   * Add conditional fields to both the form model and dynamic fields
   */
  addConditionalFields(
    fields: FormFieldConfig[],
    parentPath: string,
    context: ConditionalFieldContext,
    processNestedConditionalFields: (
      fields: FormFieldConfig[],
      parentPath: string,
    ) => void,
  ): void {
    // First add to form model to create controls
    for (const field of fields) {
      this.formStructureService.addFieldToForm(context.form, field, parentPath);
    }

    // Then add to dynamic fields for rendering
    context.updateDynamicFields((currentFields) => {
      const fieldMap = new Map(currentFields.map((f) => [f.name, f]));
      const newFields = [...currentFields];

      for (const field of fields) {
        if (!fieldMap.has(field.name)) {
          newFields.push(field);
        }
      }
      return newFields;
    });

    // Process nested conditionals after both form controls and dynamic fields are ready
    setTimeout(() => {
      processNestedConditionalFields(fields, parentPath);
    }, 0);
  }

  /**
   * Process nested conditional fields recursively
   */
  processNestedConditionalFields(
    fields: FormFieldConfig[],
    parentPath: string,
    form: FormGroup,
    callbacks: {
      processConditionalFieldsAtLevel: (
        fields: FormFieldConfig[],
        parentPath: string,
      ) => void;
      addConditionalFields: (
        fields: FormFieldConfig[],
        parentPath: string,
      ) => void;
    },
  ): void {
    // Use setTimeout to ensure form controls are fully initialized
    setTimeout(() => {
      this.processNestedFields(fields, parentPath, form, callbacks);
    }, 0);
  }

  /**
   * Helper method to process nested fields
   */
  private processNestedFields(
    fields: FormFieldConfig[],
    parentPath: string,
    form: FormGroup,
    callbacks: {
      processConditionalFieldsAtLevel: (
        fields: FormFieldConfig[],
        parentPath: string,
      ) => void;
      addConditionalFields: (
        fields: FormFieldConfig[],
        parentPath: string,
      ) => void;
    },
  ): void {
    for (const field of fields) {
      const fieldPath = parentPath ? `${parentPath}.${field.name}` : field.name;

      this.processGroupField(field, fieldPath, callbacks);
      this.processConditionalField(field, fieldPath, form, callbacks);
    }
  }

  /**
   * Process a group field
   */
  private processGroupField(
    field: FormFieldConfig,
    fieldPath: string,
    callbacks: {
      processConditionalFieldsAtLevel: (
        fields: FormFieldConfig[],
        parentPath: string,
      ) => void;
    },
  ): void {
    if (field.type === 'group' && field.fields) {
      callbacks.processConditionalFieldsAtLevel(field.fields, fieldPath);
    }
  }

  /**
   * Process a conditional field
   */
  private processConditionalField(
    field: FormFieldConfig,
    fieldPath: string,
    form: FormGroup,
    callbacks: {
      processConditionalFieldsAtLevel: (
        fields: FormFieldConfig[],
        parentPath: string,
      ) => void;
      addConditionalFields: (
        fields: FormFieldConfig[],
        parentPath: string,
      ) => void;
    },
  ): void {
    if (field.conditional !== true || !field.conditions) {
      return;
    }

    const control = form.get(fieldPath);
    if (!control) {
      return;
    }

    const value = control.value as string | number;
    if (!isDefined(value) || !isDefined(field.conditions[value])) {
      return;
    }

    // Add these conditional fields
    callbacks.addConditionalFields(field.conditions[value], fieldPath);

    // Process these fields recursively
    this.processNestedConditionalFields(
      field.conditions[value],
      fieldPath,
      form,
      callbacks,
    );
  }
}
