// form-comparison.service.ts - Handles field equality checks
import { Injectable } from '@angular/core';
import isEqualWith from 'lodash/isEqualWith';
import { FormFieldConfig } from '../../models';
import { isArray, isFunction, isObject } from '../../utils';

@Injectable({
  providedIn: 'root',
})
export class FormComparisonService {
  /**
   * Compares two field arrays for deep equality
   */
  areFieldsEqual(
    prevFields: FormFieldConfig[] | undefined,
    currentFields: FormFieldConfig[],
  ): boolean {
    if (!prevFields || prevFields.length !== currentFields.length) {
      return false;
    }

    for (let i = 0; i < currentFields.length; i++) {
      if (!this.areFieldConfigsEqual(prevFields[i], currentFields[i])) {
        return false;
      }
    }

    return true;
  }

  /**
   * Deep comparison of two FormFieldConfig objects, ignoring functions
   */
  areFieldConfigsEqual(
    fieldA: FormFieldConfig,
    fieldB: FormFieldConfig,
  ): boolean {
    // Get keys of non-function properties for fieldA
    const keysA = (Object.keys(fieldA) as (keyof FormFieldConfig)[]).filter(
      (k) => typeof fieldA[k] !== 'function',
    );
    // Get keys of non-function properties for fieldB
    const keysB = (Object.keys(fieldB) as (keyof FormFieldConfig)[]).filter(
      (k) => typeof fieldB[k] !== 'function',
    );

    if (keysA.length !== keysB.length) {
      return false;
    }

    for (const key of keysA) {
      const valA = fieldA[key];
      const valB = fieldB[key];

      if (!this.areValuesEqual(key, valA, valB)) {
        return false;
      }
    }

    return true;
  }

  /**
   * Compare two values for a given key
   */
  private areValuesEqual(
    key: keyof FormFieldConfig,
    valA: unknown,
    valB: unknown,
  ): boolean {
    // Skip function comparisons
    if (typeof valA === 'function' || typeof valB === 'function') {
      return true;
    }

    // Handle arrays
    if (isArray(valA) && isArray(valB)) {
      return this.areArrayValuesEqual(key, valA, valB);
    }

    // Handle conditions object (nested FormFieldConfig arrays)
    if (key === 'conditions' && isObject(valA) && isObject(valB)) {
      return this.areConditionsEqual(valA, valB);
    }

    // Handle objects
    if (isObject(valA) && isObject(valB)) {
      return isEqualWith(valA, valB, this.customEqualityComparator);
    }

    // Primitive comparison
    return valA === valB;
  }

  /**
   * Compare two array values for a given key
   */
  private areArrayValuesEqual(
    key: keyof FormFieldConfig,
    arrA: unknown[],
    arrB: unknown[],
  ): boolean {
    if (arrA.length !== arrB.length) {
      return false;
    }

    // Special handling for nested fields
    if (key === 'fields' || key === 'options' || key === 'treeNodeOptions') {
      return this.areArraysEqual(arrA, arrB);
    }

    return isEqualWith(arrA, arrB, this.customEqualityComparator);
  }

  /**
   * Compare two conditions objects
   */
  private areConditionsEqual(condA: object, condB: object): boolean {
    const condKeysA = Object.keys(condA);
    const condKeysB = Object.keys(condB);

    if (condKeysA.length !== condKeysB.length) {
      return false;
    }

    for (const condKey of condKeysA) {
      if (!this.isConditionKeyEqual(condKey, condA, condB, condKeysB)) {
        return false;
      }
    }

    return true;
  }

  /**
   * Check if a specific condition key is equal between two objects
   */
  private isConditionKeyEqual(
    condKey: string,
    condA: object,
    condB: object,
    condKeysB: string[],
  ): boolean {
    if (!condKeysB.includes(condKey)) {
      return false;
    }

    const condFieldsA = condA[condKey as keyof typeof condA];
    const condFieldsB = condB[condKey as keyof typeof condB];

    if (!isArray(condFieldsA) || !isArray(condFieldsB)) {
      return false;
    }

    return this.areFieldsEqual(
      condFieldsA as FormFieldConfig[],
      condFieldsB as FormFieldConfig[],
    );
  }

  /**
   * Custom equality comparator for lodash isEqualWith
   */
  private customEqualityComparator(
    valA: unknown,
    valB: unknown,
  ): boolean | undefined {
    // Skip functions
    if (isFunction(valA) || isFunction(valB)) {
      return true;
    }
    // Let lodash handle the rest
    return undefined;
  }

  /**
   * Compare two arrays for equality
   */
  private areArraysEqual(arrA: unknown[], arrB: unknown[]): boolean {
    if (arrA.length !== arrB.length) {
      return false;
    }

    for (let i = 0; i < arrA.length; i++) {
      const itemA = arrA[i];
      const itemB = arrB[i];

      if (isObject(itemA) && isObject(itemB)) {
        if (!isEqualWith(itemA, itemB, this.customEqualityComparator)) {
          return false;
        }
      } else if (itemA !== itemB) {
        return false;
      }
    }

    return true;
  }

  /**
   * Determines if field update should be skipped based on comparison
   */
  shouldSkipFieldUpdate(
    previousFields: FormFieldConfig[] | undefined,
    currentFields: FormFieldConfig[],
    updateInProgress: boolean,
    formExists: boolean,
  ): boolean {
    // Skip if update is already in progress or no form yet
    if (updateInProgress || !formExists) {
      return true;
    }

    // Skip if fields are the same reference
    if (previousFields === currentFields) {
      return true;
    }

    // Skip if fields are the same by deep comparison
    if (this.areFieldsEqual(previousFields, currentFields)) {
      return true;
    }

    return false;
  }
}
