import { Injectable, inject } from '@angular/core';
import { firstValueFrom } from 'rxjs';
import { EntitiesService, ZoneListItem, ZoneResponse } from '../../api';
import { AssetGroupingService } from './asset-grouping.service';
import { EventFilterService } from './event-filter.service';
import { ActorNodeData, TreeSelectNode } from '../models/tree-select.model';

// Public tag item interface used by component
export interface TagItem {
  id: string;
  name: string;
  filter: ActorNodeData | null; // selection descriptor
}

export interface ZoneFetchResult {
  items: ZoneListItem[];
  truncated?: { limit: number; count: number };
}

@Injectable({ providedIn: 'root' })
export class ZoneFilterService {
  private zonesCache: Record<string, Promise<ZoneFetchResult>> = {};
  private readonly entities = inject(EntitiesService);
  private readonly grouping = inject(AssetGroupingService);
  private readonly filterService = inject(EventFilterService);

  clearCache(): void {
    this.zonesCache = {};
  }

  async fetchZone(id: string): Promise<ZoneResponse | null> {
    try {
      return await firstValueFrom(this.entities.getZone(id));
    } catch {
      return null;
    }
  }

  async getZones(
    ownerId: string,
    limit: number,
    filter = '',
  ): Promise<ZoneFetchResult> {
    const safeOwner = ownerId || '';
    const key = `${safeOwner}:${filter}:${limit}`;
    if (!(key in this.zonesCache)) {
      this.zonesCache[key] = firstValueFrom(
        this.entities.listZones(
          safeOwner,
          0,
          limit,
          'name:asc',
          `owner.id=${safeOwner}&state=active${filter ? '&name=*' + encodeURIComponent(filter) + '*' : ''}`,
        ),
      ).then((res: { count: number; items: ZoneListItem[] }) => {
        const truncated =
          typeof res?.count === 'number' && res.count > limit
            ? { limit, count: res.count }
            : undefined;
        return { items: Array.isArray(res?.items) ? res.items : [], truncated };
      });
    }
    return this.zonesCache[key];
  }

  async buildGroupTree(params: {
    ownerId: string;
  }): Promise<TreeSelectNode<ActorNodeData>[]> {
    try {
      const { ownerId } = params;

      // Use getZoneGroupsAsTree to get hierarchical zone groups
      const treeItems = await this.grouping.getZoneGroupsAsTree(ownerId);

      const mapTree = (
        items: { id: string; name: string; children?: unknown[] }[],
      ): TreeSelectNode<ActorNodeData>[] => {
        return items.map((item) => ({
          id: item.id,
          label: item.name,
          data: {
            actorId: item.id,
            actorName: item.name,
            actorSelectionType: 'group',
            actorType: 'zone',
            actorTypeId: '',
            actorTypeName: 'Zone Group',
            text: item.name,
          },
          children: Array.isArray(item.children)
            ? mapTree(
                item.children as {
                  id: string;
                  name: string;
                  children?: unknown[];
                }[],
              )
            : [],
        }));
      };

      return mapTree(treeItems);
    } catch (e) {
      console.error('Failed to build group tree', e);
      return [];
    }
  }

  flattenGroupNodes(nodes: TreeSelectNode<ActorNodeData>[]): TagItem[] {
    const result: TagItem[] = [];
    const walk = (n: TreeSelectNode<ActorNodeData>[]) => {
      for (const node of n) {
        if (node.data) {
          result.push({
            id: node.id,
            name: node.data.text ?? node.label,
            filter: node.data,
          });
        }
        if (Array.isArray(node.children) && node.children.length) {
          walk(node.children);
        }
      }
    };
    walk(nodes);
    return result;
  }

  toZoneFilter(z: ZoneListItem): ActorNodeData {
    return {
      actorId: z.id,
      actorName: z.name ?? z.id,
      actorSelectionType: 'specific',
      actorType: 'zone',
      actorTypeId: '', // Zones don't usually have a type like assets do
      actorTypeName: 'Zone',
      text: `Zone "${z.name}"`,
      zoneType: z.zoneType,
    };
  }

  createSpecificFromTag(tag: TagItem): ActorNodeData {
    return (
      tag.filter ?? {
        actorId: tag.id,
        actorSelectionType: 'specific',
        actorType: 'zone',
        actorTypeId: '',
        actorTypeName: 'Zone',
        text: tag.name,
      }
    );
  }

  async search(
    term: string,
    params: {
      ownerId: string;
      multi: boolean;
      groupTreeNodes: TreeSelectNode<ActorNodeData>[];
      selectedIds: string[];
    },
  ): Promise<TagItem[]> {
    const clean = term.trim();
    if (!clean) return [];
    const { ownerId, multi, groupTreeNodes, selectedIds } = params;
    const [zoneRes, groups] = await Promise.all([
      this.getZones(ownerId, 10, clean),
      multi
        ? Promise.resolve(this.flattenGroupNodes(groupTreeNodes))
        : Promise.resolve([]),
    ]);
    const groupMatches = groups.filter((g) =>
      g.name.toLowerCase().includes(clean.toLowerCase()),
    );
    const zoneMatches: TagItem[] = zoneRes.items.map((z) => ({
      id: z.id,
      name: z.name ?? z.id,
      filter: this.toZoneFilter(z),
    }));
    return [...zoneMatches, ...groupMatches].filter(
      (x) => !selectedIds.includes(x.id),
    );
  }
}
